import React, { useState } from "react";
import { BarChart3, Calendar, Target } from "lucide-react";
import "./Dashboard.css";
import MainDashboard from "./MainDashboard";
import WeeklyPlan from "./WeeklyPlan.jsx";
import DieSummary from "./DieSummary";

const Dashboard = () => {
  const [activeView, setActiveView] = useState("weekly");

  // 🎨 Add these inline styles
  const containerStyle = {
    background: "linear-gradient(135deg, #e5e5e5 0%, rgb(229 228 231) 100%)",
    minHeight: "100vh",
    padding: "1.5rem",
    fontFamily:
      "'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif",
  };

  const headerStyle = {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: "2rem",
    flexWrap: "wrap",
    gap: "1rem",
  };

  const titleContainerStyle = {
    background:
      "linear-gradient(135deg, #6d67ca45 0%, rgb(116 82 172 / 90%) 100%)",
    backdropFilter: "blur(20px)",
    padding: "1.5rem 2rem",
    borderRadius: "20px",
    border: "1px solid rgba(255, 255, 255, 0.2)",
    boxShadow: "0 25px 50px -12px rgba(0, 0, 0, 0.25)",
  };

  const titleStyle = {
    fontSize: "2.5rem",
    fontWeight: "800",
    color: "white",
    marginBottom: "0.5rem",
    textShadow: "0 2px 10px rgba(0, 0, 0, 0.3)",
  };

  const toggleButtonStyle = (isActive) => ({
    padding: "0.75rem 1.5rem",
    fontWeight: "600",
    borderRadius: "50px",
    border: "none",
    cursor: "pointer",
    transition: "all 0.3s ease",
    background: isActive ? "rgba(255, 255, 255, 0.9)" : "transparent",
    color: isActive ? "#667eea" : "#000000",
    boxShadow: isActive ? "0 4px 15px rgba(0, 0, 0, 0.1)" : "none",
  });

  return (
    <div style={containerStyle}>
      <div style={headerStyle}>
        <div
          style={{
            display: "flex",
            gap: "0.5rem",
            background: "rgba(255, 255, 255, 0.1)",
            backdropFilter: "blur(20px)",
            padding: "0.5rem",
            borderRadius: "50px",
          }}
        >
          <button
            onClick={() => setActiveView("weekly")}
            style={toggleButtonStyle(activeView === "weekly")}
          >
            Weekly Plan
          </button>
          <button
            onClick={() => setActiveView("summary")}
            style={toggleButtonStyle(activeView === "summary")}
          >
            Summary
          </button>
        </div>
      </div>
      {/* Rest of your code remains same */}
      {activeView === "weekly" && <WeeklyPlan />}
      {activeView === "summary" && <DieSummary />}
    </div>
  );
};

export default Dashboard;
