import { useState, useEffect } from 'react';
import {
  BarChart, Bar, XAxis, YAxis, CartesianGrid,
  Tooltip, ResponsiveContainer, PieChart, Pie, Cell
} from 'recharts';
import {
  TrendingUp, Target, AlertCircle, CheckCircle,
  Clock, Factory, Settings
} from 'lucide-react';
import { productionData } from '../../data/productionData';
import productionService from '../../services/productionService';
import './ProductionDashboard.css';

const ProductionDashboard = () => {
  const [selectedDate] = useState('2023-11-15');
  const [viewMode, setViewMode] = useState('overview');
  const [data, setData] = useState(productionData);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadProductionData();
  }, [selectedDate]);

  const loadProductionData = async () => {
    setLoading(true);
    try {
      const result = await productionService.getProductionData(selectedDate);
      setData(result);
    } catch (error) {
      console.error('Error loading production data:', error);
    } finally {
      setLoading(false);
    }
  };

  const totalProduction = productionService.calculateTotalProduction(data);
  const totalPlan = productionService.calculateTotalPlan(data);
  const overallEfficiency = productionService.calculateOverallEfficiency(data);
  const chartData = productionService.prepareChartData(data);
  const pieData = productionService.preparePieData(data);

  const efficiencyData = data.map(item => ({
    name: item.id,
    efficiency: item.efficiency,
    target: 85
  }));

  return (
    <div className="production-dashboard">
      {/* Header */}
      <div className="dashboard-header">
        <div className="header-title">
          <Factory className="kpi-icon" style={{ color: '#60a5fa' }} />
          <div>
            <h1>R'gaon Press Shop</h1>
            <p className="header-subtitle">Production Dashboard - November 2023</p>
          </div>
        </div>
        <div className="view-buttons">
          <button
            onClick={() => setViewMode('overview')}
            className={`view-btn ${viewMode === 'overview' ? 'active' : ''}`}
          >
            Overview
          </button>
          <button
            onClick={() => setViewMode('detailed')}
            className={`view-btn ${viewMode === 'detailed' ? 'active' : ''}`}
          >
            Detailed
          </button>
        </div>
      </div>

      {/* KPI Cards */}
      <div className="kpi-grid">
        <div className="kpi-card green">
          <div className="kpi-content">
            <h3>Total Production</h3>
            <p>{productionService.formatNumber(totalProduction)}</p>
          </div>
          <TrendingUp className="kpi-icon" />
        </div>
        <div className="kpi-card blue">
          <div className="kpi-content">
            <h3>Total Plan</h3>
            <p>{productionService.formatNumber(totalPlan)}</p>
          </div>
          <Target className="kpi-icon" />
        </div>
        <div className="kpi-card purple">
          <div className="kpi-content">
            <h3>Overall Efficiency</h3>
            <p>{overallEfficiency}%</p>
          </div>
          <CheckCircle className="kpi-icon" />
        </div>
        <div className="kpi-card orange">
          <div className="kpi-content">
            <h3>Working Days</h3>
            <p>21</p>
          </div>
          <Clock className="kpi-icon" />
        </div>
      </div>

      {viewMode === 'overview' ? (
        <div className="charts-grid">
          {/* Production vs Plan */}
          <div className="chart-card">
            <h3 className="chart-title">
              <TrendingUp style={{ color: '#60a5fa' }} />
              Production vs Plan
            </h3>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={chartData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                <XAxis dataKey="name" stroke="#9CA3AF" />
                <YAxis stroke="#9CA3AF" />
                <Tooltip
                  contentStyle={{
                    backgroundColor: '#1F2937',
                    border: '1px solid #374151',
                    borderRadius: '8px',
                    color: 'white'
                  }}
                />
                <Bar dataKey="plan" fill="#3B82F6" name="Plan" />
                <Bar dataKey="actual" fill="#10B981" name="Actual" />
              </BarChart>
            </ResponsiveContainer>
          </div>

          {/* Production Distribution */}
          <div className="chart-card">
            <h3 className="chart-title">
              <Target style={{ color: '#a855f7' }} />
              Production Distribution
            </h3>
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={pieData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={120}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {pieData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip
                  contentStyle={{
                    backgroundColor: '#1F2937',
                    border: '1px solid #374151',
                    borderRadius: '8px',
                    color: 'white'
                  }}
                />
              </PieChart>
            </ResponsiveContainer>
          </div>

          {/* Efficiency Chart */}
          <div className="chart-card">
            <h3 className="chart-title">
              <CheckCircle style={{ color: '#10b981' }} />
              Efficiency Metrics
            </h3>
            <ResponsiveContainer width="100%" height={300}>
              <BarChart data={efficiencyData}>
                <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                <XAxis dataKey="name" stroke="#9CA3AF" />
                <YAxis stroke="#9CA3AF" />
                <Tooltip
                  contentStyle={{
                    backgroundColor: '#1F2937',
                    border: '1px solid #374151',
                    borderRadius: '8px',
                    color: 'white'
                  }}
                />
                <Bar dataKey="efficiency" fill="#8B5CF6" name="Efficiency %" />
                <Bar dataKey="target" fill="#6B7280" name="Target %" />
              </BarChart>
            </ResponsiveContainer>
          </div>

          {/* Status Cards */}
          <div className="status-section">
            <div className="status-header">
              <h3 className="chart-title">
                <AlertCircle style={{ color: '#f59e0b' }} />
                Production Status
              </h3>
            </div>
            <div className="status-items">
              {data.map((item) => (
                <div key={item.id} className="status-item">
                  <div className="status-left">
                    <div
                      className="status-indicator"
                      style={{
                        backgroundColor: productionService.getStatusColor(item.status)
                      }}
                    ></div>
                    <span>{item.id}</span>
                  </div>
                  <div className="status-right">
                    <div className="efficiency-value">{item.efficiency}%</div>
                    <div className="production-values">
                      {productionService.formatNumber(item.prodQty)} / {productionService.formatNumber(item.prodPlan)}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      ) : (
        /* Detailed View */
        <div className="detailed-table">
          <div className="table-header">
            <h3 className="chart-title">
              <Settings style={{ color: '#60a5fa' }} />
              Detailed Production Table
            </h3>
          </div>
          <div className="table-container">
            <table className="production-table">
              <thead>
                <tr>
                  <th>Product ID</th>
                  <th>Plan</th>
                  <th>Actual</th>
                  <th>Balance</th>
                  <th>Avg/Day</th>
                  <th>Req Avg</th>
                  <th>Efficiency</th>
                  <th>Status</th>
                </tr>
              </thead>
              <tbody>
                {data.map((item) => (
                  <tr key={item.id}>
                    <td style={{ fontWeight: 'bold' }}>{item.id}</td>
                    <td>{productionService.formatNumber(item.prodPlan)}</td>
                    <td style={{ color: '#10b981', fontWeight: 'bold' }}>
                      {productionService.formatNumber(item.prodQty)}
                    </td>
                    <td>{productionService.formatNumber(item.balProd)}</td>
                    <td>{productionService.formatNumber(item.avgPerDay)}</td>
                    <td>{productionService.formatNumber(item.reqAvgPerDay)}</td>
                    <td>
                      <span className={`efficiency-badge ${productionService.getEfficiencyStatus(item.efficiency)}`}>
                        {item.efficiency}%
                      </span>
                    </td>
                    <td>
                      <div
                        className="status-indicator"
                        style={{
                          backgroundColor: productionService.getStatusColor(item.status),
                          margin: '0 auto'
                        }}
                      ></div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProductionDashboard;
