\n  margin-bottom: 32px;\r\n}\r\n\r\n.chart-card {\r\n  background: rgba(30, 41, 59, 0.6);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid rgba(71, 85, 105, 0.3);\r\n  border-radius: 12px;\r\n  padding: 24px;\r\n  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.chart-title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  margin-bottom: 24px;\r\n  font-size: 1.25rem;\r\n  font-weight: 600;\r\n}\r\n\r\n.chart-title svg {\r\n  width: 20px;\r\n  height: 20px;\r\n}\r\n\r\n/* Status Table */\r\n.status-section {\r\n  background: rgba(30, 41, 59, 0.6);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid rgba(71, 85, 105, 0.3);\r\n  border-radius: 12px;\r\n  overflow: hidden;\r\n  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.status-header {\r\n  padding: 24px;\r\n  border-bottom: 1px solid rgba(71, 85, 105, 0.3);\r\n}\r\n\r\n.status-items {\r\n  padding: 24px;\r\n}\r\n\r\n.status-item {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 16px;\r\n  background: rgba(71, 85, 105, 0.3);\r\n  border-radius: 8px;\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.status-item:last-child {\r\n  margin-bottom: 0;\r\n}\r\n\r\n.status-left {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 16px;\r\n}\r\n\r\n.status-indicator {\r\n  width: 16px;\r\n  height: 16px;\r\n  border-radius: 50%;\r\n}\r\n\r\n.status-indicator.excellent {\r\n  background-color: #10b981;\r\n}\r\n\r\n.status-indicator.good {\r\n  background-color: #3b82f6;\r\n}\r\n\r\n.status-indicator.warning {\r\n  background-color: #f59e0b;\r\n}\r\n\r\n.status-indicator.danger {\r\n  background-color: #ef4444;\r\n}\r\n\r\n.status-right {\r\n  text-align: right;\r\n}\r\n\r\n.efficiency-value {\r\n  font-weight: bold;\r\n  font-size: 1.1rem;\r\n}\r\n\r\n.production-values {\r\n  font-size: 0.875rem;\r\n  color: #94a3b8;\r\n}\r\n\r\n/* Detailed Table */\r\n.detailed-table {\r\n  background: rgba(30, 41, 59, 0.6);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid rgba(71, 85, 105, 0.3);\r\n  border-radius: 12px;\r\n  overflow: hidden;\r\n  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.table-header {\r\n  padding: 24px;\r\n  border-bottom: 1px solid rgba(71, 85, 105, 0.3);\r\n}\r\n\r\n.table-container {\r\n  overflow-x: auto;\r\n}\r\n\r\n.production-table {\r\n  width: 100%;\r\n  border-collapse: collapse;\r\n}\r\n\r\n.production-table th {\r\n  background: rgba(71, 85, 105, 0.5);\r\n  padding: 16px;\r\n  text-align: left;\r\n  font-weight: 600;\r\n  border-bottom: 1px solid rgba(71, 85, 105, 0.3);\r\n}\r\n\r\n.production-table td {\r\n  padding: 16px;\r\n  border-bottom: 1px solid rgba(71, 85, 105, 0.2);\r\n}\r\n\r\n.production-table tr:nth-child(even) {\r\n  background: rgba(30, 41, 59, 0.25);\r\n}\r\n\r\n.production-table tr:hover {\r\n  background: rgba(59, 130, 246, 0.1);\r\n}\r\n\r\n.efficiency-badge {\r\n  padding: 4px 12px;\r\n  border-radius: 20px;\r\n  font-size: 0.875rem;\r\n  font-weight: 500;\r\n  text-align: center;\r\n  display: inline-block;\r\n  min-width: 60px;\r\n}\r\n\r\n.efficiency-badge.excellent {\r\n  background: rgba(16, 185, 129, 0.2);\r\n  color: #10b981;\r\n  border: 1px solid #10b981;\r\n}\r\n\r\n.efficiency-badge.good {\r\n  background: rgba(59, 130, 246, 0.2);\r\n  color: #3b82f6;\r\n  border: 1px solid #3b82f6;\r\n}\r\n\r\n.efficiency-badge.warning {\r\n  background: rgba(245, 158, 11, 0.2);\r\n  color: #f59e0b;\r\n  border: 1px solid #f59e0b;\r\n}\r\n\r\n.efficiency-badge.danger {\r\n  background: rgba(239, 68, 68, 0.2);\r\n  color: #ef4444;\r\n  border: 1px solid #ef4444;\r\n}\r\n\r\n/* Responsive Design */\r\n@media (max-width: 768px) {\r\n  .production-dashboard {\r\n    padding: 16px;\r\n  }\r\n  \r\n  .dashboard-header {\r\n    flex-direction: column;\r\n    gap: 16px;\r\n    text-align: center;\r\n  }\r\n  \r\n  .header-title h1 {\r\n    font-size: 2rem;\r\n  }\r\n  \r\n  .kpi-grid {\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n  }\r\n  \r\n  .charts-grid {\r\n    grid-template-columns: 1fr;\r\n    gap: 16px;\r\n  }\r\n  \r\n  .chart-card {\r\n    padding: 16px;\r\n  }\r\n  \r\n  .status-item {\r\n    flex-direction: column;\r\n    align-items: flex-start;\r\n    gap: 8px;\r\n  }\r\n  \r\n  .production-table {\r\n    font-size: 0.875rem;\r\n  }\r\n  \r\n  .production-table th,\r\n  .production-table td {\r\n    padding: 8px;\r\n  }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n  .header-title h1 {\r\n    font-size: 1.5rem;\r\n  }\r\n  \r\n  .kpi-content p {\r\n    font-size: 1.5rem;\r\n  }\r\n  \r\n  .chart-card {\r\n    padding: 12px;\r\n  }\r\n}\r\n\r\n/* Animation Effects */\r\n.kpi-card {\r\n  animation: fadeInUp 0.6s ease-out;\r\n}\r\n\r\n.chart-card {\r\n  animation: fadeInUp 0.8s ease-out;\r\n}\r\n\r\n.status-item {\r\n  animation: fadeInLeft 0.6s ease-out;\r\n}\r\n\r\n@keyframes fadeInUp {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(30px);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n@keyframes fadeInLeft {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateX(-30px);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translateX(0);\r\n  }\r\n}\r\n\r\n/* Hover Effects */\r\n.kpi-card:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);\r\n  transition: all 0.3s ease;\r\n}\r\n\r\n.chart-card:hover {\r\n  border-color: rgba(59, 130, 246, 0.5);\r\n  transition: border-color 0.3s ease;\r\n}\r\n\r\n.status-item:hover {\r\n  background: rgba(71, 85, 105, 0.5);\r\n  transition: background 0.3s ease;\r\n}"],"sourceRoot":""}]);
